.NavBar {
  background-color: white;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  max-width: 100%;
  margin: 0 auto;
}

.navbar-brand {
  color: #0000005f;
  font-size: 1.5rem;
  font-weight: bold;
  text-decoration: none;
  margin-right: auto;
}

.nav-links {
  display: flex;
  align-items: center;
}

.right-align {
  margin-left: auto;
}

.nav-link {
  color: #0000005f;
  text-decoration: none;
  margin-left: 1rem;
  white-space: nowrap;
}

.nav-link.active {
  color: black;
  font-weight: bold;
  border-bottom: 5px solid;
  box-sizing: border-box;
}

.nav-link:hover {
  text-decoration: none;
  color: #f0f2f5;
  font-weight: bold;
}

.navbar-brand.active {
  border-bottom: none;
}

.navbar-brand:hover {
  color: #0000005f;
  text-decoration: none;
}

/* Ensure everything stays on one line */
@media (max-width: 768px) {
  .nav-container {
    flex-wrap: nowrap;
    overflow-x: auto;
  }
  
  .nav-links {
    flex-shrink: 0;
  }
}